// dropzone
.dzu-dropzone {
  height: 130px !important;
  border: 2px dashed $primary-color !important;
  overflow: auto !important;
  border-radius: 15px;

  .dzu-inputLabel {
    padding: 0 14px;
    color: $primary-color;
    text-align: center;
    margin-bottom: 0;
    font-family: $font-roboto;
  }

  .dzu-previewImage {
    max-height: 50px;
  }

  .dz-message {
    .note {
      background-color: $transparent-color;
    }
  }

  .dzu-previewContainer {
    // padding: 16px 2%;
    border: none;

    .dzu-previewStatusContainer {
      span {
        filter: brightness(3);
      }
    }
  }

  .dzu-inputLabelWithFiles {
    background-color: $primary-color;
    color: $white;
  }

  .dzu-submitButtonContainer {
    margin: 24px;
    margin-left: auto;

    .dzu-submitButton {
      background-color: $primary-color;
      color: $white;
    }
  }
}

