// timeline
.vertical-timeline {
  &::before {
    background: $light-color;
    width: 2px;

    @media only screen and (max-width: 575px) {
      left: 14px;
    }
  }

  .vertical-timeline-element-subtitle {
    font-weight: 600;
  }

  .vertical-timeline-element {
    p {
      font-size: 14px !important;
    }

    h4 {
      @media only screen and (max-width: 575px) {
        font-size: 16px;
      }
    }

    .vertical-timeline-element-content {
      box-shadow: 1px 1px 2px 1px rgba($black, 0.08);
      background: $body-color;

      @media only screen and (max-width: 575px) {
        margin-left: 40px;
      }

      audio {
        width: 100%;
        margin-top: 15px;

        @media only screen and (max-width: 575px) {
          height: 34px;
        }
      }

      .vertical-timeline-element-date {
        font-size: 14px !important;
        padding-bottom: 0;
        font-weight: 400;
      }

      .vertical-timeline-element-content-arrow {
        border-right: 7px solid $light-color;
        display: none;
      }

      iframe {
        width: 100%;
      }
    }

    .vertical-timeline-element-icon {
      color: $white;
      border-radius: 4px;
      box-shadow: none;
      display: flex;
      align-items: center;
      justify-content: center;

      svg {
        left: 0;
        top: 0;
        margin: 0;
      }

      @media only screen and (max-width: 575px) {
        width: 30px;
        height: 30px;

        svg {
          width: 16px;
          height: 16px;
        }
      }
    }
  }

  .vertical-timeline--two-columns {
    .vertical-timeline-element-content {
      .vertical-timeline-element-date {
        top: 10px !important;
      }
    }

    .vertical-timeline-element-icon {
      height: unset;
      width: unset;
    }
  }

  @media only screen and (min-width: 1170px) {
    .vertical-timeline--two-columns {
      .vertical-timeline-element-icon {
        width: unset;
        height: unset;
        left: 50%;
        margin-left: 8px;
      }
    }
  }

  .vertical-timeline--one-column {
    &:before {
      left: 28px !important;
    }

    .vertical-timeline-element-content {
      margin-left: 90px;

      img {
        width: 500px;
      }

      .vertical-timeline-element-date {
        float: none;
        display: block;
      }
    }
  }
}

