// typeahead css
.suggestion-box {
  position: absolute;
  padding: 20px;
  border-radius: 10px;
  width: 100%;
  background-color: $white;
  box-shadow: 0 0 2px 2px #f1f3f7;
  margin-top: 5px;

  ul,
  .list-group {
    max-height: 240px;
    overflow: auto;
    display: flex;
    flex-direction: column;
    gap: 10px;

    li {
      padding: 10px 0;

      &:hover {
        span {
          color: var(--primary-color);
        }

        svg {
          stroke: var(--primary-color);
        }
      }

      a {
        display: flex;
        align-items: center;
        gap: 5px;
        color: var(--theme-body-font-color);

        svg {
          width: 18px;
          height: 18px;
        }

        span {
          line-height: 1;
          margin-bottom: -3px;
        }
      }
    }
  }

  p {
    text-align: center;
    font-size: 18px;
    color: var(--light-text);
    margin: 10px 0;
  }
}

.Typeahead {
  width: 100%;

  * {
    box-sizing: border-box;

    &:before {
      box-sizing: border-box;
    }

    &:after {
      box-sizing: border-box;
    }
  }

  .tt-hint {
    display: none !important;
  }
}

.u-cf {
  &:before {
    content: ' ';
    display: none !important;
  }

  &:after {
    content: ' ';
    display: table;
    clear: both;
  }
}

.u-hidden {
  display: none !important;
}

.u-posRelative {
  position: relative;
}

.Typeahead-spinner {
  position: absolute;
  top: 14px;
  right: 18px;
  display: none;
  color: $light-color;
}

.ProfileCard-avatar {
  svg {
    height: 18px;
    vertical-align: bottom;

    path {
      color: var(--dark-gray) !important;
    }

    polygon {
      color: var(--dark-gray) !important;
    }
  }
}

.Typeahead-hint {
  width: 100%;
  padding: 5px 8px;
  font-size: 24px;
  line-height: 30px;
  border: 1px solid var(--color-fiord);
  -webkit-border-radius: 8px;
  -moz-border-radius: 8px;
  border-radius: 8px;
  position: absolute;
  top: 0;
  left: 0;
  color: var(--gray-60);
  opacity: 1;
}

.Typeahead-menu {
  position: absolute;
  top: 60%;
  left: 0;
  padding: 0 15px 0px;
  z-index: 100;
  display: none;
  max-height: 110px;
  width: 100%;
  overflow: auto;
  background-color: $white;
  box-shadow: none;
  margin-top: 2px;
}

.Typeahead-menu.is-open {
  display: block;
  margin-top: 14px;
}

.Typeahead-selectable {
  cursor: pointer;

  + {
    .Typeahead-selectable {
      border-top: 1px solid var(--gray-60);
    }
  }
}

