@mixin btn-squre($border-radius) {
  border-radius: $border-radius + px;
}

@mixin btn-pill($border-radius) {
  border-radius: $border-radius + px;
}

@mixin btn-pill-first($radius) {
  border-radius: $radius + px 0 0 $radius + px;
}

@mixin btn-pill-last($radius) {
  border-radius: 0 $radius + px $radius + px 0;
}

.btn {
  padding: $btn-padding;

  &.bg-light-primary {
    &:hover {
      background: $primary-color !important;
      color: $white !important;
    }
  }
}

.btn-lg {
  font-size: $btn-lg-font-size;
}

.btn-sm {
  font-size: $btn-sm-font-size;
}

.btn-xs {
  padding: $btn-xs-padding;
  font-size: $btn-xs-font-size;
}

.large-btn {
  .btn {
    margin-right: 5px;
    margin-bottom: 15px;
  }

  margin-bottom: -20px;
}

.btn-outline-white {
  border: 1px solid $white;
  color: $white;

  &:hover,
  &:focus {
    background: $white;
  }
}

// outline buttons//
@each $btn-name, $btn-color in (primary, $primary-color), (secondary, $secondary-color), (success, $success-color),
  (danger, $danger-color), (info, $info-color), (light, $light-color), (dark, $dark-color), (warning, $warning-color)
{
  .btn-air-#{$btn-name} {
    box-shadow: 0 5px 10px 2px rgba(88, 103, 221, 0.19) !important;

    &:hover,
    &:active,
    &:not([disabled]):not(.disabled):active {
      background-color: darken($btn-color, 10%);
      border-color: darken($btn-color, 10%);
    }
  }

  .input-air-#{$btn-name} {
    box-shadow: 0 3px 5px 1px rgba($btn-color, 0.1) !important;

    &:focus {
      border-color: $btn-color;
    }
  }
}

.btn-gradient {
  color: $white !important;
  font-weight: 500;
  padding: 10px 30px;
  border-radius: 5px;
  background: $gradient-primary;
  letter-spacing: 0.06rem;

  &:hover {
    background-size: 100% !important;
  }
}

.btn-warning {
  color: $white;

  &:hover,
  &.disabled {
    color: $white;
  }
}

[class*="-gradient"] {
  &:hover {
    background-size: 50% 100%;
    transition: all 0.3s ease;
    color: $white;
  }
}

.btn-square {
  @include btn-squre(0);
}

.btn-pill {
  @include btn-squre(60);
}

.btn-group-pill {
  .btn {
    &:first-child {
      @include btn-pill-first(60);
    }

    &:last-child {
      @include btn-pill-last(60);
    }
  }
}

.btn-group-square {
  .btn {
    &:first-child {
      @include btn-pill-first(0);
    }

    &:last-child {
      @include btn-pill-last(0);
    }
  }
}

.btn-showcase {
  .btn {
    margin-bottom: 10px;
    margin-right: 10px;
  }

  margin-bottom: -10px;
}

.btn-dropdown-showcase {
  .dropdown {
    margin-bottom: 10px;
    margin-right: 18px;
  }

  .btn-group {
    margin-bottom: 10px;
    margin-right: 18px;
  }

  margin-bottom: -10px;
}

.btn-group {
  .radio {
    label {
      &::before {
        top: 2px;
      }

      &::after {
        top: 7px;
      }
    }
  }

  .checkbox {
    label {
      margin-top: 3px;
    }
  }
}

.btn-group-showcase {
  > div {
    margin-bottom: -20px;

    label {
      margin-bottom: 0;
    }
  }

  .btn-group {
    margin-right: 20px;
    margin-bottom: 20px;
  }

  .checkbox {
    input[type="checkbox"] {
      display: none;
    }
  }
}

.tooltiptext {
  visibility: visible;
  width: 120px;
  background-color: #555;
  color: $white;
  text-align: center;
  border-radius: 6px;
  padding: 5px 0;
  position: absolute;
  z-index: 1;
  bottom: 125%;
  left: 50%;
  margin-left: -60px;
  opacity: 1;
  transition: opacity 0.3s;

  &::after {
    content: "";
    position: absolute;
    top: 100%;
    left: 50%;
    margin-left: -5px;
    border-width: 5px;
    border-style: solid;
    border-color: #555 transparent transparent transparent;
  }
}

// Default button on hover
.card-header-right-icon {
  .btn {
    &.badge-light-primary {
      &:hover {
        color: var(--white);
        background-color: $primary-color;
      }
    }
  }
}

.btn-outline-primary {
  &:hover {
    background-color: $primary-color !important;
  }
}

.btn-info {
  :not(.btn-check) + .btn:active {
    color: white;
  }
}

.btn-group-horizontal,
.btn-group-vertical {
  .dropdown {
    .dropdown-toggle {
      padding: 0.375rem 1.75rem;
      width: 100%;
      border-radius: 0.375rem;
      border-top-left-radius: 0;
      border-bottom-left-radius: 0;
      color: black;
    }
  }
}

.btn-group-vertical {
  .dropdown {
    .dropdown-toggle {
      &.btn-success,
      &.btn-danger {
        color: $white;
      }
    }
  }

  .dropdown {
    &:last-child {
      .dropdown-toggle {
        border-radius: 0.375rem;
        border-top-left-radius: 0 !important;
        border-top-right-radius: 0 !important;
      }
    }
  }
}

.main-checkbox-toggle {
  label {
    &.btn {
      &:hover {
        color: var(--white);
      }
    }
  }
}

/**=====================
    2.9 Buttons CSS end
==========================**/

.btn-gray-light {
  color: var(--bs-white);
  background-color: var(--bs-gray-400);
  border-color: var(--bs-gray-300);

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.hover {
    transition: 1.5s;
    color: var(--bs-gray-100);
    background-color: var(--bs-gray-500);
    border-color: var(--bs-gray-400);
  }
}

.btn-gray {
  color: var(--bs-white);
  background-color: var(--bs-gray-600);
  border-color: var(--bs-gray-500);

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.hover {
    transition: 1.5s;
    color: var(--bs-gray-200);
    background-color: var(--bs-gray-700);
    border-color: var(--bs-gray-600);
  }
}

.btn-dotted {
  border: none;
  border-radius: 0;

  &:hover,
  &:focus,
  &:active,
  &.active,
  &.hover {
    border: none;
    border-radius: 0;
  }
}
