/**=====================
    2.12 Datatable CSS Start
==========================**/
div {
  &.dt-button-background {
    display: none !important;
  }

  &.DTS {
    div {
      &.dataTables_scrollBody {
        table {
          z-index: 1;
        }
      }
    }
  }

  .table-avtar {
    height: 32px;
    margin-right: 10px;

    [dir="rtl"] & {
      margin-right: unset;
      margin-left: 10px;
    }
  }

  .action {
    display: flex;

    i {
      font-size: 16px;
    }

    .pdf {
      i {
        font-size: 20px;
        color: $danger-color;
      }
    }

    .view {
      margin-right: 5px;

      i {
        font-size: 14px;
        color: $font-gray-color;
      }

      [dir="rtl"] & {
        margin-left: 5px;
      }
    }

    .edit {
      margin-right: 5px;

      i {
        color: $success-color;
      }

      [dir="rtl"] & {
        margin-left: 5px;
      }
    }

    .restore {
      margin-right: 5px;

      i {
        color: $warning-color;
      }

      [dir="rtl"] & {
        margin-left: 5px;
      }
    }

    .delete {
      i {
        color: $danger-color;
      }
    }
  }
}

#auto-generate-content_wrapper {
  &.dataTables_wrapper {
    button {
      background-color: white;
      border: 1px solid var(--theme-default);
      color: var(--theme-default);
    }
  }
}

.dataTables_wrapper {
  width: calc(100% - 1px);

  .input-group {
    .btn {
      z-index: 0;
    }
  }

  // .rdt_Pagination {
  //   border-top-style: none !important;
  //   border-top-width: 0px !important;
  // }

  .rdt_TableHeader {
    min-height: 65px;
    padding-left: 0px;
    padding-right: 0px;
    transition: all 0.3s ease-in-out;

    div > div {
      font-size: 1rem;
    }

    div {
      padding-left: 16px;
      padding-right: 16px;
    }
  }

  .rdt_TableHeaderWithSubHeader {
    margin-bottom: -65px;
  }

  header {
    min-height: 65px;
    padding-left: 0px;
    padding-right: 0px;
    transition: all 0.3s ease-in-out;
  }

  .rdt_TableRow {
    &:hover {
      border-bottom-color: rgba(0, 0, 0, 0.3);
      // outline-style: none;
      outline-width: 1px;
      outline-color: #b8b3b3;
    }
  }

  &.no-footer {
    .dataTables_scrollBody {
      border-bottom: 1px solid $light-gray;
    }
  }

  .dt-buttons {
    .btn {
      border-radius: 0.25rem !important;
    }
  }

  padding: 0;

  .btn-group {
    button {
      margin-right: 5px;
    }
  }

  button {
    font-weight: 400;
    padding: 0.375rem 0.75rem;
    font-size: 14px;
    border-radius: 0.25rem;
    color: var(--bs-gray-600);
  }

  & + .dataTables_wrapper {
    margin-top: 20px;
  }

  .dataTables_paginate {
    margin-left: 15px !important;
    border: 1px solid $light-color;
    border-radius: 0.25rem;
    padding-top: 0;

    .paginate_button {
      margin: 0;

      &.current,
      &:active {
        background: var(--theme-default);
        color: $white !important;
        box-shadow: none;
      }

      &:hover {
        border: 1px solid var(--theme-default);
        color: $dark-color !important;
        background: transparent !important;
      }
    }
  }

  .dataTables_length {
    margin-bottom: 30px;

    select {
      border-color: $light-color;
      color: $dark-color;
      padding: 0 10px;
      margin: 0 10px;
      height: 2.7142em;
      background-color: $white;
    }
  }

  table.dataTable {
    border: 1px solid $light-semi-gray;

    tbody {
      td,
      th {
        &.select-checkbox {
          padding-right: 40px !important;

          &:before {
            right: 20px;
            top: 22px;
            left: unset;
          }

          &:after {
            margin-top: -5px;
            margin-left: 21px;
          }
        }
      }
    }

    thead {
      th,
      td {
        border-bottom: 2px solid $light-semi-gray;
      }
    }

    th,
    td {
      padding: 0.75rem;
    }
  }

  .dataTables_filter {
    width: 100%;
    display: flex;
    transition: all 0.3s ease-in-out;

    input[type="search"] {
      border: 1px solid $light-semi-gray;
      padding: 0 10px;
      margin-left: 10px;
      height: 37px;
      border-radius: 0;
      transition: all 0.3s ease-in-out;
    }
  }
}

.dataTables_scrollHeadInner {
  width: 100% !important;
}

table {
  .fixedHeader-floating {
    position: fixed !important;
    background-color: $white;
  }

  .box {
    > div {
      padding: 5px 15px;
      background-color: var(--theme-default);
      display: inline-block;
      color: $white;
      border-radius: 5px;
      margin-bottom: 15px;
      margin-right: 15px;
    }
  }

  &.dataTable {
    &.fixedHeader-locked,
    &.fixedHeader-floating {
      width: calc(100vw - 250px) !important;
      max-width: calc(100vw - 250px) !important;
      overflow: hidden !important;
      right: 0 !important;
      z-index: 99;
      left: 260px !important;
    }

    &:not(.fixedHeader-locked) {
      &:not(.fixedHeader-floating) {
        width: 100% !important;
      }
    }
  }

  &.dataTable {
    border-collapse: collapse !important;
    margin-top: 0 !important;

    thead {
      .sorting,
      .sorting_asc,
      .sorting_desc,
      .sorting_asc_disabled,
      .sorting_desc_disabled {
        &:before,
        &:after {
          bottom: 12px !important;
        }
      }
    }

    input,
    select {
      border: 1px solid $light-semi-gray;
      height: 37px;
    }
  }
}

.user-datatable {
  tr {
    td,
    th {
      &:first-child {
        min-width: 150px;
      }
    }
  }
}

.jsgrid {
  .jsgrid-button {
    width: 20px;
    height: 20px;
  }

  .jsgrid-delete-button {
    background-position: 0 -78px;
    width: 22px;
    height: 22px;
  }

  .jsgrid-edit-button {
    background-position: -2px -122px;
    width: 22px;
    height: 22px;
  }

  .jsgrid-insert-mode-button {
    background-position: -2px -160px;
    width: 25px;
    height: 25px;
  }

  .jsgrid-insert-button {
    background-position: -3px -202px;
    width: 16px;
    height: 16px;
  }
}

.toolbar {
  padding: 5px 15px;
  background-color: var(--theme-default);
  display: inline-block;
  color: $white;
  border-radius: 5px;
  margin-bottom: 15px;
  margin-right: 15px;
}

code {
  &.option {
    color: $danger-color !important;
  }

  &.api {
    color: $success-color !important;
  }
}

// Datatable extension
.dt-ext {
  .dataTables_wrapper {
    .page-item {
      &.active {
        .page-link {
          background-color: var(--theme-default) !important;
          border-color: var(--theme-default) !important;
          color: $white;
        }
      }
    }

    .dataTables_paginate {
      border: none;

      .paginate_button {
        padding: 0;
        border: none;

        &.disabled,
        &.hover,
        &.active {
          border: none !important;
        }
      }

      .page-link {
        margin-left: 0px;
        color: var(--theme-default);
      }
    }

    button.dt-button,
    div.dt-button,
    a.dt-button,
    button.dt-button:focus:not(.disabled),
    div.dt-button:focus:not(.disabled),
    a.dt-button:focus:not(.disabled),
    button.dt-button:active:not(.disabled),
    button.dt-button.active:not(.disabled),
    div.dt-button:active:not(.disabled),
    div.dt-button.active:not(.disabled),
    a.dt-button:active:not(.disabled),
    a.dt-button.active:not(.disabled) {
      background-color: var(--theme-default);
      border-color: var(--theme-default);
      border-radius: 2px;
      color: $white;
      background-image: none;
      font-size: 14px;
    }

    button.dt-button.btn-success,
    div.dt-button.btn-success,
    a.dt-button.btn-success {
      background-color: $success-color;
      border-color: $success-color;
    }

    button.dt-button.btn-primary,
    div.dt-button.btn-primary,
    a.dt-button.btn-primary {
      background-color: var(--theme-default);
      border-color: var(--theme-default);
    }

    button.dt-button.btn-danger,
    div.dt-button.btn-danger,
    a.dt-button.btn-danger {
      background-color: $danger-color;
      border-color: $danger-color;
    }

    table.dataTable th.focus,
    table.dataTable td.focus {
      outline: 3px solid var(--theme-default);
    }

    table.dataTable.display tbody > tr.odd.selected > .sorting_1,
    table.dataTable.order-column.stripe tbody > tr.odd.selected > .sorting_1 {
      background-color: lighten($primary-color, 38%);
    }

    table.dataTable.stripe tbody > tr.odd.selected,
    table.dataTable.stripe tbody > tr.odd > .selected,
    table.dataTable.display tbody > tr.odd.selected,
    table.dataTable.display tbody > tr.odd > .selected {
      background-color: lighten($primary-color, 38%);
    }

    table.dataTable.display tbody > tr.even.selected > .sorting_1,
    table.dataTable.order-column.stripe tbody > tr.even.selected > .sorting_1 {
      background-color: lighten($primary-color, 38%);
    }

    table.dataTable tbody > tr.selected,
    table.dataTable tbody > tr > .selected {
      background-color: lighten($primary-color, 38%);
    }
  }
}

td.highlight {
  background-color: $light-color !important;
}

td.details-control {
  background: url("/assets/images/details_open.png") no-repeat center center;
  cursor: pointer;
}

tr.shown td.details-control {
  background: url("/assets/images/details_close.png") no-repeat center center;
}

.dataTables_scroll {
  & ~ .dataTables_paginate {
    margin-top: 20px !important;
  }
}

.product-table {
  th {
    &:last-child {
      min-width: 120px;
    }
  }

  h6 {
    font-weight: 600;
    color: $dark-color;
  }
}

table {
  &.dataTable {
    &.display {
      tbody {
        tr {
          &.odd {
            > .sorting_1 {
              background-color: $light;
            }

            .badge {
              line-height: 15px;
            }
          }

          &.even {
            .badge {
              line-height: 15px;
            }
          }
        }
      }
    }
  }
}

// JS Grid Table
.jsgrid-row,
.jsgrid-alt-row {
  .jsgrid-cell {
    .badge {
      line-height: 15px !important;
    }
  }
}

.table-responsive {
  .rdt_Pagination {
    display: flex;
    justify-content: space-between;

    .fqFocD,
    .YpOAo {
      display: none;
    }

    .iIqieJ {
      margin: 0;
      font-size: 14px;
      color: rgba($black, 0.8);
    }

    .fxMZcL {
      &:hover {
        &:not(:disabled) {
          background-color: rgba($primary-color, 0.3);
        }
      }

      &:disabled {
        svg {
          fill: rgba($primary-color, 0.5);
        }
      }

      svg {
        fill: $primary-color;
      }

      &:focus {
        background-color: rgba($primary-color, 0.3);
      }
    }
  }

  .rdt_Table {
    border: 1px solid rgba($black, 0.12);

    .rdt_TableBody {
      .rdt_TableRow {
        &:nth-child(odd) {
          .rdt_TableCell {
            font-size: 14px;

            &:nth-child(1) {
              background-color: #fafafa;
            }

            &:nth-child(n + 2) {
              background-color: #fff;
            }
          }
        }

        &:nth-child(even) {
          .rdt_TableCell {
            &:nth-child(1) {
              background-color: #fff;
            }

            &:nth-child(n + 2) {
              background-color: #fafafa;
            }
          }
        }
      }
    }

    .rdt_TableHead {
      .rdt_TableHeadRow {
        .rdt_TableCol {
          font-size: 14px;
        }
      }
    }
  }

  .IgpLZ,
  .kvgeKk {
    &:hover {
      border-bottom-color: rgba($black, 0.12);
      outline-color: rgba($black, 0.12);
    }
  }

  .jpPQTS {
    border-radius: 0;
  }
}

.custom-filter-main {
  .dataTables_filter {
    display: block;
    margin: 0;

    tr {
      td {
        display: inline-block;
        margin-bottom: 8px;
      }
    }
  }
}

/**=====================
     2.12 Datatable CSS Ends
==========================**/
