/**=====================
    5.8 Rtl CSS Start
==========================**/
html[dir="rtl"] {
  // list
  .horizontal-list-wrapper {
    .list-group-horizontal-sm,
    .list-group-horizontal-md,
    .list-group-horizontal-lg,
    .list-group-horizontal-xl,
    .list-group-horizontal-xxl {
      .list-group-item {
        &:first-child {
          border-bottom-left-radius: 6px;
          border-top-right-radius: 6px;
          border-bottom-right-radius: 6px;
          border-top-left-radius: 6px;
        }

        &:last-child {
          border-top-right-radius: 0;
          border-bottom-left-radius: 6px;
          border-top-left-radius: 6px;
          border-bottom-right-radius: 0;
          border-left-width: 1px;
        }
      }
    }
  }

  ul {
    padding-right: 0;
  }

  .ms-auto {
    margin-left: unset !important;
    margin-right: auto !important;
  }

  .slider-wrapper {
    direction: ltr;
  }

  .f-right {
    float: left;
  }

  .papernote-wrap {
    .note-labels {
      ul {
        gap: 9px;
      }
    }
  }

  .form-check-inline {
    margin-right: unset;
    margin-left: 1rem;
  }

  .color-box {
    button {
      + button {
        margin-left: unset;
        margin-right: 5px;
      }
    }
  }

  .knowledgebase-search {
    .form-inline {
      svg {
        left: unset;
        right: 20px;
      }
    }
  }

  .mobile-app-card {
    .card-body {
      .wavy {
        margin-left: -167px;
        margin-right: unset;
      }
    }
  }

  .switch-showcase {
    .form-check {
      .form-check-input {
        float: left;
      }
    }
  }

  // range-slider
  .range-slider {
    direction: ltr;
  }

  // dropdown
  .heading-dropdown {
    &.rtl-dropdown {
      .dropdown {
        &.show {
          .dropdown-menu {
            transform: translate(-75px, 35px) !important;
          }
        }
      }
    }
  }

  .pill-input-group span {
    &:last-child {
      border-top-right-radius: unset !important;
      border-bottom-right-radius: unset !important;
      border-top-left-radius: 1.3rem !important;
      border-bottom-left-radius: 1.3rem !important;
    }

    &:first-child {
      border-top-left-radius: unset !important;
      border-top-right-radius: unset !important;
      border-top-right-radius: 1.3rem !important;
      border-bottom-right-radius: 1.3rem !important;
    }
  }

  .page-wrapper {
    &.compact-wrapper {
      .page-body-wrapper {
        .sidebar-wrapper {
          text-align: right;

          .toggle-sidebar {
            right: unset;
            left: 30px;
          }

          .sidebar-main .sidebar-links {
            padding: 0;

            .simplebar-wrapper {
              .simplebar-mask {
                .simplebar-content-wrapper {
                  .simplebar-content {
                    > li .badge {
                      left: 50px !important;
                      right: unset !important;
                    }
                  }
                }
              }
            }
          }

          &.close_icon {
            margin-right: 0;
            margin-left: unset;

            .logo-icon-wrapper {
              text-align: left;

              img {
                margin-left: 8px;
              }
            }

            ~ .page-body,
            ~ footer {
              margin-left: 0;
              margin-right: 100px;
              transition: 0.5s;
            }

            .sidebar-main {
              .sidebar-links {
                .simplebar-wrapper {
                  .simplebar-mask {
                    .simplebar-content-wrapper {
                      .simplebar-content {
                        > li {
                          a {
                            svg {
                              margin-left: 0;
                            }
                          }
                        }
                      }
                    }
                  }
                }

                .sidebar-list {
                  .sidebar-title {
                    &.active {
                      ~ .sidebar-submenu {
                        display: none !important;
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              display: none;
            }

            &:hover {
              .sidebar-main {
                .sidebar-links {
                  .simplebar-wrapper {
                    .simplebar-mask {
                      .simplebar-content-wrapper {
                        .simplebar-content {
                          > li {
                            a {
                              svg {
                                margin-left: 13px;
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }

          &:hover {
            transform: translate(0px);

            .sidebar-main {
              .sidebar-links {
                .sidebar-list {
                  .sidebar-title {
                    &.active {
                      ~ .sidebar-submenu {
                        display: block !important;
                      }
                    }
                  }
                }

                .simplebar-wrapper {
                  .simplebar-mask {
                    .simplebar-content-wrapper {
                      .simplebar-content {
                        > li {
                          a {
                            svg {
                              float: none;
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }

            .according-menu {
              display: block;
            }
          }

          .sidebar-main {
            .sidebar-links {
              .simplebar-wrapper {
                .simplebar-mask {
                  .simplebar-content-wrapper {
                    .simplebar-content {
                      > li {
                        .sidebar-submenu {
                          &:after {
                            right: 30px;
                            left: unset;
                          }

                          > li {
                            a {
                              padding-right: 45px !important;
                              padding-left: unset !important;

                              &:after {
                                right: 26px;
                                left: unset;
                              }
                            }
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .page-body {
          margin-left: unset;
          margin-right: 265px;
        }

        .sidebar-wrapper {
          .sidebar-main {
            .sidebar-links {
              .simplebar-wrapper {
                .simplebar-mask {
                  .simplebar-content-wrapper {
                    .simplebar-content {
                      > li {
                        a {
                          svg {
                            margin-right: unset;
                            margin-left: 15px;
                          }
                        }
                      }
                    }
                  }
                }
              }
            }
          }
        }

        .according-menu {
          right: unset;
          left: 20px;
        }
      }
    }

    &.only-body {
      .page-header {
        .header-wrapper {
          .nav-right {
            right: unset;
            left: 30px;
          }
        }
      }
    }
  }

  // task page css
  .date-details {
    > div {
      + div {
        margin-left: unset;
        margin-right: 10px;
      }
    }
  }

  // contact page css
  .contact-editform {
    &.ps-0 {
      padding-left: 20px !important;
    }
  }

  .contact-options {
    li {
      .btn-category {
        padding-left: unset !important;
        padding-right: 40px;
      }
    }
  }

  .list-persons {
    .profile-mail {
      .email-general {
        ul {
          padding-right: unset;
          padding-left: 20px;

          li {
            > span {
              float: left;
            }
          }
        }
      }

      .media {
        .media-body {
          ul {
            li {
              + li {
                padding-left: unset;
                padding-right: 10px;
                margin-left: unset;
                margin-right: 5px;
                border-left: unset;
                border-right: 1px solid $light-color;
              }
            }
          }
        }
      }
    }
  }

  // file manager css
  .file-content {
    .form-inline {
      i {
        padding-right: unset;
        padding-left: 10px;
      }
    }
  }

  .file-sidebar {
    .btn {
      svg {
        margin-right: unset;
        margin-left: 8px;
      }
    }
  }

  // projects page css
  .form-select {
    background-position: left 0.75rem center;
  }

  .customers {
    ul {
      li {
        + li {
          margin-left: unset;
          margin-right: -10%;
        }
      }
    }
  }

  .project-list {
    .border-tab {
      &.nav-tabs {
        .nav-item {
          .nav-link {
            padding: 5px 0 5px 30px;
          }
        }
      }
    }

    .btn {
      @media (max-width: 767px) {
        float: right;
      }
    }
  }

  .project-box {
    .badge {
      right: unset;
      left: 30px;
    }
  }

  // File manager
  .file-manager {
    .files,
    .folder {
      .file-box,
      .folder-box {
        margin-left: 8px;
        margin-right: unset;
      }
    }
  }

  // kanban board css
  .kanban-board {
    float: right;
  }

  .kanban-item {
    .kanban-box {
      .customers {
        ul {
          li {
            + li {
              margin-left: unset;
              margin-right: -10px;
            }
          }
        }
      }
    }

    .list {
      li {
        i {
          margin-right: unset;
          margin-left: 10px;
        }
      }
    }
  }

  //horizontal-sidebar
  .page-wrapper {
    &.horizontal-wrapper {
      .header-logo-wrapper {
        text-align: right;
      }

      .page-body-wrapper {
        .page-body {
          margin-right: 0;
        }

        footer {
          margin-right: 0;
        }
      }
    }

    &.material-type {
      .page-body-wrapper {
        .page-body {
          margin-right: auto;
        }
      }
    }
  }

  .page-header {
    .header-wrapper {
      .nav-right {
        > ul {
          > li {
            &:first-child {
              margin-right: 0;
            }
          }
        }

        &.right-header {
          ul {
            li {
              .profile-media {
                .media-body {
                  margin-right: 15px;
                  margin-left: unset;
                }
              }
            }
          }
        }
      }

      .toggle-sidebar {
        margin-left: 2px;
        margin-right: unset;
      }
    }
  }

  // compact icon css
  .page-wrapper {
    .page-header {
      .header-wrapper {
        .nav-right {
          .notification-dropdown {
            left: -20px !important;
            right: unset !important;

            @media (max-width: 575.98px) {
              left: 12px !important;
            }
          }
        }

        .search-full {
          input {
            padding-left: unset;
            padding-right: 60px;
            text-align: right;
          }

          .form-group {
            &:before {
              left: unset;
              right: 30px;
            }

            .close-search {
              right: unset;
              left: 0;
            }
          }
        }
      }
    }

    &.compact-sidebar {
      .toggle-sidebar {
        margin-left: 15px;
        margin-right: unset;
      }

      .page-header {
        margin-left: unset;
        margin-right: 150px;

        &.close_icon {
          margin-right: 0;
        }
      }

      .page-body-wrapper {
        .page-body {
          margin-left: unset;
          margin-right: 150px;
        }

        .footer {
          margin-left: unset;
          margin-right: 150px;
        }

        div.sidebar-wrapper {
          > div {
            text-align: right;
          }

          .sidebar-main {
            .sidebar-links {
              li {
                a {
                  &.active {
                    ~ .sidebar-submenu,
                    ~ .mega-menu-container {
                      animation: fadeInRight 1000ms ease-in-out;
                    }
                  }
                }

                .sidebar-submenu,
                .mega-menu-container {
                  right: 150px;
                  left: unset;

                  li {
                    a {
                      padding: 15px 40px 15px 15px;

                      .according-menu {
                        right: 20px;
                      }
                    }
                  }

                  > li {
                    > a {
                      &::after {
                        left: unset;
                        right: 20px;
                      }
                    }
                  }

                  .mega-box {
                    .link-section {
                      text-align: right;

                      .submenu-title {
                        h5 {
                          padding: 15px 40px 15px 15px;

                          &::after {
                            left: unset;
                            right: 20px;
                          }
                        }
                      }
                    }
                  }
                }
              }

              .sidebar-list {
                .badge {
                  left: unset;
                  right: 53%;
                }

                .sidebar-link {
                  text-align: center;
                }
              }

              .mega-menu {
                text-align: center;

                .according-menu {
                  right: unset;
                  left: 20px;
                }
              }
            }
          }

          &.close_icon {
            transform: translateX(150px);

            ~ .page-body,
            ~ .footer {
              margin-right: 0px;
            }
          }
        }
      }
    }

    &.compact-small {
      .page-header {
        margin-right: 90px;
      }

      .page-body-wrapper {
        div.sidebar-wrapper {
          .sidebar-main {
            .sidebar-links {
              li {
                .sidebar-submenu,
                .mega-menu-container {
                  right: 90px;
                }
              }
            }
          }
        }

        .footer,
        .page-body {
          margin-right: 90px;
        }
      }
    }

    &.material-icon {
      .page-header {
        padding-left: unset;
        padding-right: 90px;
        margin: 0 auto;

        &.close_icon {
          padding-right: 0;
        }
      }

      .page-body-wrapper {
        .page-body {
          padding-left: 15px;
          padding-right: 90px;
          margin: 0 auto;
        }

        div.sidebar-wrapper {
          &.close_icon {
            ~ .page-body {
              padding-right: 15px;
              margin: 0 auto !important;
            }
          }
        }
      }
    }
  }

  .apexcharts-canvas {
    direction: ltr;
  }

  // bookmark app css
  .email-wrap {
    .btn-mail {
      text-align: right;
    }

    .email-app-sidebar {
      ul {
        li {
          a {
            padding-left: unset;
            padding-right: 20px;
          }
        }
      }
    }
  }

  .bookmark-tabcontent {
    .details-bookmark {
      .bookmark-card {
        &.card {
          .favourite-icon {
            text-align: center;
          }

          .title-bookmark {
            text-align: right;
          }
        }
      }
    }
  }

  .bookmark-wrap {
    .details-bookmark {
      .row {
        > div {
          + div {
            padding-left: 15px !important;
          }
        }
      }
    }
  }

  // index page css start
  .appointment-table {
    table {
      tr {
        td {
          .status-circle {
            left: unset;
            right: 35px;
          }
        }
      }
    }
  }

  .flag-icons {
    div {
      .media-body {
        h5 {
          margin-left: 5px;
          margin-right: unset;
        }
      }
    }
  }

  .notification {
    .card {
      .d-flex {
        .w-100 {
          p {
            .badge {
              margin-left: unset;
              margin-right: 10px;
            }
          }
        }
      }
    }
  }

  // index page css end
  @each $progress-gradient-name, $progress-gradient-color in (primary, var(--theme-default)),
    (secondary, $secondary-color), (success, $success-color), (danger, $danger-color), (info, $info-color),
    (light, $light-color), (dark, $dark-color), (warning, $warning-color)
  {
    .progress-gradient-#{$progress-gradient-name} {
      background-image: linear-gradient(to right, $progress-gradient-color, $transparent-color);

      &::after {
        right: unset;
        left: -1px;
      }

      .animate-circle {
        right: unset;
        left: -8px;
      }
    }
  }

  .progress-animate {
    span {
      left: -10px;
      right: unset;
    }
  }

  // ecommerce dashboard css end
  .me-4 {
    margin-left: 1.5rem !important;
    margin-right: unset !important;
  }

  .pe-4 {
    padding-left: 1.5rem !important;
    padding-right: unset !important;
  }

  .me-1 {
    margin-left: 0.25rem !important;
    margin-right: unset !important;
  }

  .navs-dropdown {
    .onhover-show-div {
      right: 0;
    }
  }

  .default-according {
    &.style-1 {
      button {
        text-align: right;

        &:before {
          left: 20px;
          right: unset;

          @media (max-width: 575px) {
            left: 15px;
          }
        }

        i {
          right: 18px;
          left: unset;
        }
      }
    }

    .card {
      .card-header {
        i {
          right: 18px;
          left: unset;
        }
      }
    }
  }

  .faq-wrap {
    .faq-accordion {
      .card {
        .card-header {
          .btn-link {
            padding-right: 20px !important;

            @media (max-width: 575.98px) {
              padding-right: 10px !important;
            }
          }
        }
      }
    }
  }

  .ProfileCard-details {
    padding-right: 30px;
    padding-left: unset;
    float: right;
  }

  .starter-main {
    .card-body {
      ul {
        padding-right: 30px;
        padding-left: unset;
      }
    }
  }

  .theme-tab {
    .tab-title {
      li {
        a {
          svg {
            margin-left: 5px;
            margin-right: unset;
          }
        }
      }
    }
  }

  .spent {
    .spent-graph {
      .project-budget {
        margin-left: 30px;
        margin-right: unset;
      }
    }
  }

  .support-table {
    table {
      tbody {
        tr {
          td {
            &:first-child {
              padding-right: 0;
              padding-left: unset;
            }
          }
        }
      }
    }
  }

  .order-box {
    .sub-total,
    .total,
    .qty {
      .shipping-class,
      li {
        .shopping-checkout-option,
        span {
          float: left;
        }
      }

      li {
        .count {
          float: left;
        }
      }
    }
  }

  .img-paypal {
    margin-right: 15px;
    margin-left: unset;
  }

  .cart {
    .qty-box {
      .input-group {
        .btn {
          border-radius: 0 !important;
        }
      }
    }
  }

  .product-qnty {
    fieldset {
      .input-group {
        .input-group-prepend {
          .btn {
            border-top-right-radius: 0;
            border-bottom-right-radius: 0;
          }
        }
      }
    }
  }

  .grid-options {
    margin-right: 10px;
    margin-left: unset;

    ul {
      li {
        a {
          .line-grid-1 {
            right: 12px;
            left: unset;
          }

          .line-grid-2 {
            right: 18px;
            left: unset;
          }

          .line-grid-3 {
            right: 36px;
            left: unset;
          }

          .line-grid-4 {
            right: 42px;
            left: unset;
          }

          .line-grid-5 {
            right: 48px;
            left: unset;
          }

          .line-grid-6 {
            right: 66px;
            left: unset;
          }

          .line-grid-7 {
            right: 72px;
            left: unset;
          }

          .line-grid-8 {
            right: 78px;
            left: unset;
          }

          .line-grid-9 {
            right: 84px;
            left: unset;
          }

          .line-grid-10 {
            right: 103px;
            left: unset;
          }

          .line-grid-11 {
            right: 109px;
            left: unset;
          }

          .line-grid-12 {
            right: 115px;
            left: unset;
          }

          .line-grid-13 {
            right: 121px;
            left: unset;
          }

          .line-grid-14 {
            right: 127px;
            left: unset;
          }

          .line-grid-15 {
            right: 133px;
            left: unset;
          }
        }
      }
    }
  }

  .product-filter {
    &.new-products {
      .owl-theme {
        .owl-nav {
          left: 0;
          right: unset;
        }

        .owl-item {
          .item {
            .product-box {
              .product-details {
                &.text-start {
                  text-align: left !important;
                }
              }
            }
          }
        }
      }
    }
  }

  .product-sidebar {
    .filter-section {
      .card {
        .card-header {
          h6 {
            .pull-right {
              i {
                left: auto;
                right: unset;
              }
            }
          }
        }
      }
    }
  }

  .prooduct-details-box {
    .btn {
      right: unset;
      left: 15px;
    }

    .close {
      right: unset;
      left: 10px;
    }
  }

  .collection-filter-block {
    svg {
      margin-right: unset;
      margin-left: 10px;
    }
  }

  .feature-products {
    form {
      .form-group {
        i {
          left: 30px;
          right: unset;
        }
      }
    }
  }

  .product-wrapper-grid {
    &.list-view {
      .product-box {
        .product-details {
          text-align: right;
        }
      }
    }
  }

  .page-wrapper {
    .page-body-wrapper {
      .page-title {
        .breadcrumb {
          margin-left: unset;
        }
      }
    }
  }

  .bookmark {
    ul {
      li {
        + li {
          margin-left: unset;
          margin-right: 10px;
        }

        .search-form {
          .form-control-search {
            left: -50px;
            right: unset;

            &:before {
              right: 50px;
              left: unset;
            }

            &:after {
              right: 20px;
              left: unset;
            }
          }
        }
      }
    }
  }

  .me-0 {
    margin-left: 0 !important;
    margin-right: unset !important;
  }

  .footer {
    .pull-right {
      float: none;
    }
  }

  .checkbox_animated,
  .radio_animated {
    margin: 0 8px 0 16px;
  }

  .button.remove {
    left: -10px;
    right: unset;
  }

  .code-box-copy__btn {
    left: 11px;
    right: unset;
  }

  ul {
    &.nav-menus {
      border-bottom: none;
    }

    &.notification-dropdown {
      &.onhover-show-div {
        li {
          text-align: right;
        }
      }
    }
  }

  .badge {
    + .badge {
      margin-right: 5px;
      margin-left: unset;
    }
  }

  .offset-xl-3 {
    margin-right: 25%;
    margin-left: unset;
  }

  .m-l-10 {
    margin-right: 10px !important;
    margin-left: unset !important;
  }

  .m-r-20 {
