.switch-showcase {
    margin-bottom: -8px;

    .media {
        line-height: 1;
    }

    .form-check {
        .form-check-input {
            float: right;
        }
    }
}

.form-switch {
    .form-check-input {
        &:checked {
            background-color: var(--theme-default);
            border-color: var(--theme-default);
        }
    }
}

// Custom design switches
.tg-list {
    text-align: center;
    display: flex;
    align-items: center;
}

.tgl {
    display: none;

    // add default box-sizing for this scope
    &,
    &:after,
    &:before,
    & *,
    & *:after,
    & *:before,
    & + .tgl-btn {
        box-sizing: border-box;

        &::selection {
            background: none;
        }
    }

    + .tgl-btn {
        outline: 0;
        display: block;
        width: 4em;
        height: 2em;
        position: relative;
        cursor: pointer;
        user-select: none;

        &:after,
        &:before {
            position: relative;
            display: block;
            content: "";
            width: 50%;
            height: 100%;
        }

        &:after {
            left: 0;
        }

        &:before {
            display: none;
        }
    }

    &:checked + .tgl-btn:after {
        left: 50%;
    }
}

.tgl-skewed {
    + .tgl-btn {
        overflow: hidden;
        transform: skew(-10deg);
        backface-visibility: hidden;
        transition: all 0.2s ease;
        background: $secondary-color;

        &:after,
        &:before {
            transform: skew(10deg);
            display: inline-block;
            transition: all 0.2s ease;
            width: 100%;
            text-align: center;
            position: absolute;
            line-height: 2em;
            font-weight: bold;
            color: #fff;
            text-shadow: 0 1px 0 rgba(0, 0, 0, 0.4);
        }

        &:after {
            left: 100%;
            content: attr(data-tg-on);
        }

        &:before {
            left: 0;
            content: attr(data-tg-off);
        }

        &:active {
            background: $secondary-color;

            &:before {
                left: -10%;
            }
        }
    }

    &:checked + .tgl-btn {
        background: $primary-color;

        &:before {
            left: -100%;
        }

        &:after {
            left: 0;
        }

        &:active:after {
            left: 10%;
        }
    }
}

.tgl-flip {
    + .tgl-btn {
        padding: 2px;
        transition: all 0.2s ease;
        perspective: 100px;

        &:after,
        &:before {
            display: inline-block;
            transition: all 0.4s ease;
            width: 100%;
            text-align: center;
            position: absolute;
            line-height: 2em;
            font-weight: bold;
            color: #fff;
            position: absolute;
            top: 0;
            left: 0;
            backface-visibility: hidden;
            border-radius: 4px;
        }

        &:after {
            content: attr(data-tg-on);
            background: $warning-color;
            transform: rotateY(-180deg);
        }

        &:before {
            background: $danger-color;
            content: attr(data-tg-off);
        }

        &:active:before {
            transform: rotateY(-20deg);
        }
    }

    &:checked + .tgl-btn {
        &:before {
            transform: rotateY(180deg);
        }

        &:after {
            transform: rotateY(0);
            left: 0;
            background: $success-color;
        }

        &:active:after {
            transform: rotateY(20deg);
        }
    }
}

.radius-none {
    .form-check-input {
        border-radius: 3px;
    }
}

// Square Checked
.tg-list {
    .tg-list-item {
        .square-checked {
            .switch-state {
                &::before {
                    content: "";
                    border-radius: 0;
                }
            }
        }
    }
}

.switch-wrapper {
    .media {
        .col-form-label {
            padding-bottom: 0;
        }
    }

    .tg-list {
        p {
            margin: 0 8px 0 0;
        }
    }
}

.switch-sizing {
    .media {
        .form-label {
            margin-bottom: 0;
        }

        .media-body {
            .form-label {
                margin-bottom: 0;
            }
        }
    }
}

.default-square {
    .form-switch {
        .form-check-input {
            background-image: url(/assets/images/switch/square-gray.png);

            &:checked {
                background-image: url(/assets/images/switch/square.svg);
            }
        }
    }
}

